<template>
	<div class="block">
		<h1>{{ product.title }}</h1>
		<router-link :to="{name: 'products'}">Back to products</router-link>
		<hr>
			<!-- <div class="alert alert-success">
				{{ product.price }}
			</div> -->
			<router-link tag="h3" :to="'/waldax'">
			<div style="display: flex;">
				<div v-for="product in product.version">
					<a style="text-decoration: none; cursor: pointer">
						<img style="width: 100%" :src="product.src" alt="">
						<p style="text-align: center; font-size: 19px">
						{{ product.name }}</p>
					</a>
				</div>
			</div>
			</router-link>
		<hr>
		hw: add to cart
	</div>
</template>

<script>
import {mapGetters} from 'vuex';
import {mapActions} from 'vuex';

	export default {
		computed: {
				...mapGetters('waldax', {
					modules: 'modules'
				}),
				...mapGetters('products', {
					products: 'items'
				}),
				...mapGetters('cart', {
					inCart: 'products'
				}),
			id(){
				return this.$route.params.id;
			},
			product(){
				return this.$store.getters['products/item'](this.id);
			}
		},
		// methods: {
		// 	...mapActions('cart', {
		// 		addToCart: 'add',
		// 		removeFromCart: 'remove'
		// 	})
		// }
	}
</script>
