<template>
		<div>
			<section class="section__inner">
				<router-link :to="'/products/waldax'"><h3>Go back</h3></router-link>
				<hr>


				<!-- <router-link :to="{name: 'waldax'}">Back to products</router-link> -->
				<h1 style="text-align: center;">Варианты размеров</h1>
				<br><br>
				<splide :options="options">
					<splide-slide style="text-align: center;" v-for="(item, key) in modules.straightModules" :key="key">
						<h2>{{ item.size }}</h2>
						<!-- <img style="max-height: 120px;" :src="item.src"> -->
						<img :src="item.src" style="height: 120px;" />
					</splide-slide>
				</splide>
			</section>
	</div>
</template>

<script>
	import {mapGetters} from 'vuex';
	import {mapActions} from 'vuex';


	export default {
		data() {
		 return {
			 options: {
				type   : 'fade',
				perPage: 1,
				height: 200,
				gap : '0'
			 },
		 };
	 },
		computed: {
			id(){
				return this.$route.params.id;
			},
			product(){
				return this.$store.getters['products/item'](this.id);
			},
			...mapGetters('waldax', {
				modules: 'modules'
			}),
			...mapGetters('products', {
				products: 'items'
			}),
			...mapGetters('cart', {
				inCart: 'products'
			})
		}
	}
</script>
<style scoped>
	.straight__inner {
		display: flex;
		flex-direction: row;
		text-align: center;
		width: auto;
	}
	.straight__inner p {
		font-size: 29px;
	}
	.straight__image {
	}
	.image {
		width: auto;
	}
	.block {
		width: 900px;
		display: flex;
	}
</style>
